class Scene {
  constructor() {
    const options = {
      damping: 0.01,
      alwaysShowTracks: true,
      continuousScrolling: false
    }
    this.scrollBar = Scrollbar.init(document.querySelector('#my-scrollbar'), options);
    console.log(this.scrollBar);
    this.container = document.getElementById("scene");
    this.width = window.innerWidth;
    this.height = window.innerHeight;
    this.aspect = this.width / this.height
    this.frustum = 10;
    this.camera = new THREE.OrthographicCamera(
      this.frustum * this.aspect / - 2,
      this.frustum * this.aspect / 2,
      this.frustum / 2,
      this.frustum / -2,
      0,
      1000
    );
    this.camera.position.z = 10;
    this.scene = new THREE.Scene();
    this.clock = new THREE.Clock();
    this.renderer = new THREE.WebGLRenderer({
      canvas: this.container,
      antialias: true,
      alpha: true,
    });
    this.renderer.setSize(this.width, this.height);
    this.renderer.setPixelRatio(2);
    // this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.loader = new GLTFLoader();
    this.donut = null;
    this.group = new THREE.Group();
    this.material = new THREE.MeshLambertMaterial( { color: 0x00ff00 } );
    
    this.endScene = false
    this.setupResize();
    this.resize();
    
    this.addObject();
    this.addLight();
    this.bindEvents();
    this.render();
    // this.scrolling();
    
  }
  setupResize() {
    window.addEventListener("resize", this.resize.bind(this));
  }
  resize() {
    this.aspect = window.innerWidth / window.innerHeight;  
    this.camera.left = this.frustum * this.aspect / - 2;
    this.camera.right = this.frustum * this.aspect / 2;
    this.camera.top = this.frustum / 2;
    this.camera.bottom = - this.frustum / 2;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(window.innerWidth, window.innerHeight);

  }
  bindEvents() {
    document.addEventListener("scroll", (e) => {
      
      
      

    });
  }
  addObject() {
    this.loader.load(donut, (donut) => {
      donut.scene.children[2].position.y = -0.015;
      donut.scene.children[2].rotation.x = Math.PI / 4;
      this.donut = donut.scene.children[2];
      isMobile() ? this.donut.scale.set(10,10,10) : this.donut.scale.set(20,20,20)
      this.mat2 = this.donut.children[0].material.clone()
      this.donut2 = this.donut.clone()
      this.mat2.color.r = 1.0
      this.mat2.color.g = 0
      this.mat2.color.b = 0
      this.donut2.children[0].material = this.mat2
      this.donut2.scale.set(0.01,0.01,0.01)
      this.donut2.visible = false
      console.log(this.donut2);
      this.group.add(this.donut, this.donut2);
    });
    this.scene.add(this.group);
  }
  addLight() {
    this.light = new THREE.SpotLight(0xffffff, 2);
    this.light.position.set(0, 5, 7);
    this.scene.add(this.light);
  }
  animate() {
    if (this.scrollBar.offset.y > 0) {
      gsap.to(this.group.position, {
        x: 0,
        z: 0,
        duration: 1,
      })
      gsap.to(this.group.scale, {
        x: 1,
        y: 1,
        z: 1,
        duration:1
      })
      gsap.to(this.group.rotation, {
        x: 0,
        duration: 1,
      })
      gsap.to(this.donut2.scale, {
        x: 0.01,
        y: 0.01,
        z: 0.01,
        duration: 0.5,
      })
      gsap.to(this.donut2.position, {
        x: 0,
        duration: 1,
          onFinish: ()=>{
            this.endScene = false
          }
      })
        
      if(this.scrollBar.offset.y > this.height *0.5) {
        gsap.to(this.group.position, {
          x: isMobile() ? -2 : -7,
          z: 0,
          duration: 1,
        })
      }
      if(this.scrollBar.offset.y > this.height *1.5) {
        gsap.to(this.group.position, {
          x:isMobile() ? 3.5 : 12,
          z: 0,
          duration: 1,
        })
      }
      if(this.scrollBar.offset.y > this.height *2.5) {
        
        gsap.to(this.group.rotation, {
          x: 0.5,
          duration: 1,
        })
        this.donut2.visible = true
        this.endScene = true
        const scale = isMobile() ? 20 : 38 
        gsap.to(this.donut2.scale, {
          x: scale,
          y: scale,
          z: scale,
          duration: 1,
        })
        gsap.to(this.donut2.position, {
          x: isMobile() ? -3.5 : -7,
          duration: 1,
        })
        gsap.to(this.group.scale, {
          x: 1,
          y: 1,
          z: 1,
          duration:1,
          
        })
        
      }
    }
    this.container.style.top = this.scrollBar.offset.y + "px";
    if (this.donut) this.donut.rotation.y += 0.005;
    if (this.endScene === true) this.donut2.rotation.y += 0.005;
  }


  

  render() {
    this.timer = Date.now() * 0.0002;
    this.animate();
    this.renderer.render(this.scene, this.camera);
    window.requestAnimationFrame(this.render.bind(this));
  }
}

new Scene();
